.dashboard-new-ticket {
  padding: 0 20px;
  .btn {
    max-width:220px;
  }
  .form {
    max-width:920px;
    div {
      &:nth-of-type(1) {
        color: rgba(255,255,255,0.86) !important;
        font-weight: 300;
      }
      &:nth-of-type(2) {
        color: rgba(255,255,255,0.86) !important;
        font-weight: 300;
      }
    }


    .MuiSelect-select {
      padding: 27px 12px 14px 15px;
      letter-spacing:0.5px;
      font-size: 15px;
    }
    .MuiFilledInput-root {
      margin-bottom: 20px;
    }

    .MessageTextArea {
      .MuiInputBase-root {
        padding-left: 15px;
        letter-spacing:0.5px;
        font-size: 15px;
        textarea {
          color: rgba(255,255,255,0.86) !important;
        }
      }
      label {
        font-size: 15px !important;
        transform: translate(16px, 10px) scale(0.75);
        letter-spacing:0.5px;

      }
    }
  }
}